.card {
  pointer-events: auto;
  width: 100%;
  max-height: 630px;
  position: relative;
}

@media screen and (min-width: 320px) {
  .card {
    max-height: 100%;
    width: calc(50% - 15px);
  }

  .card:not(:last-child) {
    margin-bottom: 40px;
  }
}

@media screen and (min-width: 768px) {
  .card {
    width: calc(33.3333% - 10px);
    max-height: 630px;
  }
}

@media (min-width: 1280px) {
  .card {
    width: calc(33.3333% - 20px);
  }

  .card:not(:last-child) {
    margin-bottom: 0;
  }
}

